*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}

.content {
  height: 100vh;
}

.navbar{
  background-color:rgb(238, 225, 238) !important;
}

.resgister-page{
  
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;

}


/* transection page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding:15px 20px;
  box-shadow:0 0 3px rgb(222, 44, 44);
  margin-bottom: 10px;
}

/* transection page */

/* antd icons */
.switch-icons{
  border: 1px solid rgba(0,0,0,0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: black;
}
.inactive-icon{
  color: gray;
}
.row{
  overflow: hidden;
}

.filter-tab .ant-select-selector {
  width: 150px !important;
}